import React from 'react'
import PropTypes from 'prop-types'
import { Divider, Menu, Icon, Input, List, Image, Button, Segment, Header, Item, Checkbox } from 'semantic-ui-react'
import { compose, withStateHandlers } from 'recompose'

const types = {
  BEAVER: { name: 'Beaver', icon: './icons/beaver.png' },
  DAM: { name: 'Dam', icon: './icons/dam.png' },
  BDA: { name: 'BDA', icon: './icons/bda.png' },
  LODGE: { name: 'Lodge', icon: './icons/lodge.png' },
  SCENTMOUND: { name: 'Scent Mound', icon: './icons/scentmound.png' }
}

const TheForm = ({ value, activeType, handleChange, changeType }) => (
  <div id='InputForm'>
    <Button.Group className='inputFormMenu' fluid size='mini'>
      {Object.keys(types).map(t => (
        <TabMenuItem
          name={types[t].name}
          icon={types[t].icon}
          active={types[t].name === activeType}
          onClick={() => changeType(types[t].name)}
        />
      ))}
    </Button.Group>

    { activeType === types.BEAVER.name ? <TheFormBeaver /> : null }
    { activeType === types.DAM.name ? <TheFormDam /> : null }
    { activeType === types.BDA.name ? <TheFormBDA /> : null }
    { activeType === types.LODGE.name ? <TheFormLodge /> : null }
    { activeType === types.SCENTMOUND.name ? <TheFormScentMound /> : null }

    <Menu color='blue' inverted className='cameraMenu' size='large'>
      <Menu.Item header>
        Add a photo
      </Menu.Item>
      <Menu.Menu position='right'>
        <Menu.Item>
          <Button icon='file' inverted />
        </Menu.Item>
        <Menu.Item>
          <Button icon='camera' inverted />
        </Menu.Item>
      </Menu.Menu>
    </Menu>
    <Divider />
    { activeType ? <Button fluid size='large' icon='check' content='Done' positive /> : null}
  </div>
)

TheForm.propTypes = {
  value: PropTypes.string,
  activeType: PropTypes.string,
  handleChange: PropTypes.func,
  changeType: PropTypes.func
}

export default compose(
  withStateHandlers({
    activeType: null,
    value: 'sm'
  }, {
    handleChange: () => (e, { value }) => ({ value }),
    changeType: () => (activeType) => ({ activeType })
  })
)(TheForm)

const TabMenuItem = ({ name, icon, color, ...props }) => (
  <Button style={{ background: color }} {...props}>
    <Image src={icon} size='mini' />
    {name}
  </Button>
)

TabMenuItem.propTypes = {
  name: PropTypes.string,
  icon: PropTypes.string,
  color: PropTypes.string
}

const TheFormBeaver = () => (
  <List>
    <List.Item>
      <List.Content floated='right'>
        <Input type='number' style={{ width: '50px' }} value={2} />
      </List.Content>
      <List.Content>
        <List.Header>Number:</List.Header>
        <List.Description>How many did you see?</List.Description>
      </List.Content>
    </List.Item>
  </List>
)

