import React from 'react'
import PropTypes from 'prop-types'
import { Container, List, Image } from 'semantic-ui-react'
import Topbar from './Topbar'
import './style.css'

const ProjectList = ({ projects }) => (
  <div className='PageWrap'>
    <Topbar />
    <Container>
      <List divided selection style={{ cursor: 'pointer' }}>
        {projects.map((project, idx) => (
          <List.Item key={`ProjectItem${idx}`}
            style={{ minHeight: 120 }}
          >
            <Image size='medium' src={project.Banner} />
            <List.Content>
              <List.Header as='h2'>{project.Title}</List.Header>
              <List.Description>{project.Title}</List.Description>
            </List.Content>
            <List.Content floated='right'>
              <List.Description>{project.Watershed}</List.Description>
              <List.Description>{project.Stream}</List.Description>
            </List.Content>
          </List.Item>
        ))}
      </List>
    </Container>
  </div>
)

ProjectList.propTypes = {
  projects: PropTypes.array
}

export default ProjectList
