import React from 'react'
import PropTypes from 'prop-types'
import BaseMap from './BaseMap'
import { Icon, Popup } from 'semantic-ui-react'
import { AutoSizer } from 'react-virtualized'
import { Marker } from 'react-map-gl'
import { findBounds, findCenter } from '../../../lib/mapUtil'
import { compose, withProps } from 'recompose'

const MarkerMap = ({ data, center, bounds, ...props }) => (
  <div style={{ minHeight: '100%' }}>
    <AutoSizer>
      {({ height, width }) => (
        <BaseMap
          {...props}
          maxBounds={bounds}
          longitude={center[0]}
          latitude={center[1]}
          size={{ height: height < 500 ? 500 : height, width }}
        >
          {
            // Render props here
            (viewport) => data.map(({ latitude, longitude }) => (
              <Marker viewport={viewport}
                longitude={longitude}
                latitude={latitude}
                offsetLeft={-20}
                offsetTop={-30}
              >
                <Popup
                  flowing
                  trigger={
                    <Icon
                      className='markerIcon'
                      name='map marker'
                      size='large'
                      color='red'
                      style={{ textShadow: '2px 2px 2px #00000099' }}
                    />
                  }>
                  Hello: ({latitude}, {longitude})
                </Popup>
              </Marker>
            ))
          }
        </BaseMap>
      )}
    </AutoSizer>
  </div>
)

MarkerMap.propTypes = {
  data: PropTypes.arrayOf(PropTypes.shape({
    latitude: PropTypes.number,
    longitude: PropTypes.number
  }))
}

export default compose(
  withProps(({ data }) => ({
    center: findCenter(data.map(d => ([d.longitude, d.latitude]))),
    bounds: findBounds(data.map(d => ([d.longitude, d.latitude])))
  }))
)(MarkerMap)
