import React from 'react'
import PropTypes from 'prop-types'
import { Icon, Label, Image } from 'semantic-ui-react'
import BaseMap from '../../common/maps/BaseMap'
import { AutoSizer } from 'react-virtualized'
import { Marker } from 'react-map-gl'

const MarkerMap = ({ latitude, longitude, height, ...props }) => (
  <div style={{ height: height }}>
    <AutoSizer>
      {({ width }) => (
        <BaseMap
          {...props}
          longitude={longitude}
          latitude={latitude}
          size={{ height, width }}
        >
          {
            // Render props here
            (viewport) => (
              <Marker viewport={viewport}
                longitude={viewport.longitude}
                latitude={viewport.latitude}
                offsetLeft={-15}
                offsetTop={-15}
              >
                <Image src='./icons/beaver.png' style={{ height: '30px' }} />
              </Marker>
            )
          }
          <Label icon>
            <Icon name='hand pointer' />
          Tap to place marker
          </Label>
        </BaseMap>
      )}
    </AutoSizer>
  </div>
)

MarkerMap.propTypes = {
  latitude: PropTypes.number,
  longitude: PropTypes.number,
  height: PropTypes.number
}

export default MarkerMap
