import HomeFeed from './HomeFeed'
import ProjectList from './ProjectList'
import ProjectMap from './ProjectMap'
import ProjectsMap from './ProjectsMap'
import StructureHome from './StructureHome'
import config from '../../config'
import { name } from 'faker'
import { randomMap, randomArrayEl } from '../../lib/util'
import _ from 'lodash'
import { data as projectData } from './projects.json'

HomeFeed.displayName = 'Projects/Home'
ProjectsMap.displayName = 'Projects/Map'
ProjectList.displayName = 'Project/List'
ProjectMap.displayName = 'Project/Map'
StructureHome.displayName = 'Structure/Home'

const images = [
  '/images/bdas/1/DJI_0137.jpg',
  '/images/bdas/2/Structure08_04.jpg',
  '/images/bdas/3/DJI_0767.jpg',
  '/images/bdas/4/IMG_0117.jpg',
  '/images/bdas/5/Juniper_BDA_Sep_21_2016.jpg',
  '/images/bdas/7/DJI_0004.jpg',
  '/images/bdas/6/Myers_Creek_Before-during-after-from-road.jpg'
]

/**
 * Make us a random user profile
 */
const getFill = () => {
  const size = _.random(100, 200)
  return `https://fillmurray.com/${size}/${size}`
}
const users = []
_.times(_.random(10, 20), () => users.push({
  name: `${name.firstName()} ${name.lastName()}`,
  avatar: getFill(),
  bdas: randomMap(1, 5, () => randomArrayEl(images))
}))

const fixtures = [
  {
    name: 'HomeFeed',
    component: HomeFeed,
    props: {
      users
    }
  },
  {
    name: 'Project List',
    component: ProjectList,
    props: {
      projects: projectData.Projects
    }
  },
  {
    name: 'Project Map',
    component: ProjectMap
  },
  {
    name: 'Projects Map',
    component: ProjectsMap,
    props: {
      projects: projectData.Projects
    }
  },
  {
    name: 'Structure Home',
    component: StructureHome
  }
]
const devFixtures = [

]

const finalFixes = [...fixtures, ...(config.env === 'development' ? devFixtures : [])].map(fix => {
  fix.component.displayName = `BDA Main App/Questions/${fix.component.name}`
  return fix
})

export default finalFixes
