import React from 'react'
import PropTypes from 'prop-types'

import {
  FlexibleWidthXYPlot,
  XAxis,
  YAxis,
  Borders,
  HorizontalGridLines,
  VerticalGridLines,
  VerticalBarSeries
} from 'react-vis'

const BarChart = ({ data, xAxis, yAxis, height }) => (
  <FlexibleWidthXYPlot height={height}>
    <HorizontalGridLines />
    <VerticalGridLines />

    { data.map((dseries, idx) => <VerticalBarSeries key={`series${idx}`} data={dseries} />) }

    <Borders style={{ all: { fill: '#fff' } }} />

    <XAxis />
    <XAxis hideLine hideTicks
      title={xAxis}
      top={height} position='end'
      style={{ fontSize: 13, fontWeight: 'bold' }}
    />
    <YAxis />
    <YAxis hideLine hideTicks
      title={yAxis}
      style={{ fontSize: 13, fontWeight: 'bold' }}
    />

  </FlexibleWidthXYPlot>

)

BarChart.propTypes = {
  data: PropTypes.arrayOf(PropTypes.shape({
    x: PropTypes.number,
    y: PropTypes.number
  })),
  xAxis: PropTypes.string,
  yAxis: PropTypes.string,
  height: PropTypes.number
}
BarChart.defaultProps = {
  data: [],
  xAxis: '',
  yAxis: '',
  height: 300
}

export default BarChart
