import React from 'react'
import PropTypes from 'prop-types'
import { Icon, Menu, Header, Button } from 'semantic-ui-react'
import BaseMap from '../../common/maps/BaseMap'
import { AutoSizer } from 'react-virtualized'
import { Marker } from 'react-map-gl'

const MarkerMap = ({ latitude, longitude, ...props }) => (
  <div style={{ minHeight: '100%' }}>
    <Menu fixed='bottom' inverted vertical fluid>
      <Menu.Item header icon><Icon name='crosshairs' /> Place the crosshairs where you see a structure</Menu.Item>
      <Menu.Item header icon><Button sized='large' icon='check' content='Capture' color='blue' fluid /></Menu.Item>
    </Menu>
    <AutoSizer>
      {({ height, width }) => (
        <BaseMap
          {...props}
          longitude={longitude}
          latitude={latitude}
          size={{ height: height < 500 ? 500 : height, width }}
        >
          {
            // Render props here
            (viewport) => ([
              <Marker viewport={viewport}
                longitude={longitude}
                latitude={latitude}
                offsetLeft={-20}
                offsetTop={-30}
              >
                <Header style={{ textShadow: '1px 1px 2px #FFFFFF' }}>
                  <Icon
                    style={{ textShadow: '1px 1px 2px #FFFFFF' }}
                    name='map marker'
                    size='big'
                    color='white'
                  />You</Header>
              </Marker>,
              <Marker viewport={viewport}
                longitude={viewport.longitude}
                latitude={viewport.latitude}
                offsetLeft={-20}
                offsetTop={-20}
              >
                <Icon
                  style={{ textShadow: '1px 1px 2px #00000099' }}
                  name='crosshairs'
                  size='big'
                  color='red'
                />
              </Marker>
            ]

            )
          }
        </BaseMap>
      )}
    </AutoSizer>
  </div>
)

MarkerMap.propTypes = {
  latitude: PropTypes.number,
  longitude: PropTypes.number,
  hereLat: PropTypes.number,
  hereLng: PropTypes.number
}

export default MarkerMap
