import React from 'react'
import PropTypes from 'prop-types'
import { Step, Segment, Header, Divider, Responsive } from 'semantic-ui-react'
import { compose, withState, withProps } from 'recompose'

const TheForm = ({ activeStep, steps, stepHeaders }) => {
  return (
    <div>
      <Step.Group attached='top'
        ordered size='mini'
        unstackable fluid>
        {stepHeaders}
        <Step link disabled>
          <Responsive minWidth='800'>
            <Step.Content>
              <Step.Title>Confirmation</Step.Title>
            </Step.Content>
          </Responsive>
        </Step>

      </Step.Group>

      <Segment attached>
        <Responsive maxWidth='800'>
          <Header>Step {activeStep + 1}: {steps[activeStep].title}</Header>
          <p>{steps[activeStep].description}</p>
          <Divider />
        </Responsive>
        {steps[activeStep || 0].content}
      </Segment>
    </div>
  )
}

TheForm.propTypes = {
  activeStep: PropTypes.number,
  steps: PropTypes.array,
  stepHeaders: PropTypes.arrayOf(PropTypes.node)
}

export default compose(
  withState('activeStep', 'setActiveStep', 0),
  withProps(props => {
    return Object.assign({}, props, {
      stepHeaders: props.steps.map(({ title, description, complete }, idx) => (
        <Step link key={idx}
          active={idx === props.activeStep}
          completed={!!complete} onClick={() => props.setActiveStep(idx)}>
          <Responsive minWidth='800'>
            <Step.Content>
              <Step.Title>{title}</Step.Title>
              <Step.Description>{description}</Step.Description>
            </Step.Content>
          </Responsive>
        </Step>
      ))
    })
  })
)(TheForm)
