import React from 'react'
import PropTypes from 'prop-types'
import { Feed, Icon, Divider, Tab } from 'semantic-ui-react'
import Topbar from './Topbar'
import { lorem } from 'faker'
import { randomMap, randomArrayEl } from '../../lib/util'
import _ from 'lodash'
import './style.css'

const HomeFeed = ({ users }) => (
  <div className='PageWrap'>
    <Topbar />
    <div className='tabWrap'>
      <Tab menu={{ secondary: true, pointing: true }} panes={
        [
          { menuItem: 'Feed',
            render: () => <Tab.Pane attached={false}>
              <Feed>
                {randomMap(10, 20, () => {
                  const Comp = randomArrayEl([FeedItemAddBDA, FeedItemAddFriend, FeedItemPost])
                  return <Comp profile={randomArrayEl(users)} />
                })}
              </Feed>
            </Tab.Pane> },
          { menuItem: 'Near Me',
            render: () => <Tab.Pane attached={false}>
              <Feed>
                {randomMap(10, 20, () => <FeedItemAddBDA profile={randomArrayEl(users)} />)}
              </Feed>
            </Tab.Pane> }
        ]
      } />
    </div>
  </div>
)
HomeFeed.propTypes = {
  users: PropTypes.array
}

export default HomeFeed

const FeedItemPost = ({ profile }) => (
  <Feed.Event>
    <Feed.Label image={profile.avatar} />
    <Feed.Content>
      <Feed.Summary>
        <a>{profile.name}</a> posted on their page
        <Feed.Date>{_.random(1, 10)} days ago</Feed.Date>
      </Feed.Summary>
      <Feed.Extra text>
        {lorem.paragraph()}
      </Feed.Extra>
      <RandomLikes />
      <Divider />
    </Feed.Content>
  </Feed.Event>
)

FeedItemPost.propTypes = {
  profile: PropTypes.object
}

const FeedItemAddFriend = ({ profile }) => (
  <Feed.Event>
    <Feed.Label image={profile.avatar} />
    <Feed.Content>
      <Feed.Summary>
        <Feed.User>{profile.name}</Feed.User> added you as a friend
        <Feed.Date>{_.random(1, 10)} Hour Ago</Feed.Date>
      </Feed.Summary>
      <RandomLikes />
      <Divider />
    </Feed.Content>
  </Feed.Event>
)

FeedItemAddFriend.propTypes = {
  profile: PropTypes.object
}

const FeedItemAddBDA = ({ profile }) => (
  <Feed.Event>
    <Feed.Label image={profile.avatar} />
    <Feed.Content>
      <Feed.Summary>
        <a>{profile.name}</a> discovered <a>{profile.bdas.length} new dams</a>
        <Feed.Date>{_.random(1, 10)} days ago</Feed.Date>
      </Feed.Summary>
      <Feed.Extra images>
        {profile.bdas.map((bda, idx) => (
          <a key={`${profile.name}${idx}`}>
            <img src={bda} />
          </a>
        ))}
      </Feed.Extra>
      <RandomLikes />
      <Divider />
    </Feed.Content>
  </Feed.Event>
)

FeedItemAddBDA.propTypes = {
  profile: PropTypes.object
}

const RandomLikes = () => (
  <Feed.Meta>
    <Feed.Like>
      <Icon name='like' />{_.random(10, 20000)} Likes
    </Feed.Like>
  </Feed.Meta>
)
