const devFixtures = devInitFixtures.reduce((acc, fix) => {
  acc.push({
    wrapForm: true,
    reduxState: reduxHelper(),
    ...fix,
    props: {
      present: false,
      ...fix.props
    }
  })
  acc.push({
    wrapForm: true,
    reduxState: reduxHelper(),
    ...fix,
    name: fix.name + ' (Present)',
    props: {
      present: true,
      ...fix.props
    }
  })
  return acc
}, [])

const finalFixes = [...(config.env === 'development' ? devFixtures : [])].map(fix => {
  fix.component.displayName = `BDA Mobile App/Questions/${fix.name}`
  return fix
})

export default finalFixes
