import React from 'react'
import PropTypes from 'prop-types'
import { Checkbox, Popup, Label, Menu, Button, Segment, Header } from 'semantic-ui-react'
import moment from 'moment'
import _ from 'lodash'
import './ProjList.css'
import { withProps } from 'recompose'

const ProjList = ({ structures }) => (
  <Segment basic className='projList' style={{ paddingTop: '40px' }}>
    <Menu fixed='top'>
      <Menu.Item>
        <Button icon='upload' color='blue' content='Sync All' />
      </Menu.Item>
      <Menu.Item>
        <Checkbox toggle label='Unsynced Only' />
      </Menu.Item>
    </Menu>
    {/* <List divided selection> */}
    {Object.keys(structures).map((guid, idx) =>
      <StructureItemProps guid={guid} key={`structItem${idx}`}
        {...structures[guid]} />
    )}
    {/* </List> */}
  </Segment>
)

ProjList.propTypes = {
  structures: PropTypes.object
}

const StructureItem = ({ guid, fields, uploaded, uploadFailed, uploading, color }) => (
  <Segment raised color={color}>
    <Header as='a'>{getHeader(fields)}</Header>
    <p>{getMeta(fields)}</p>
    <Label.Group className='projListDownloadLabels'>
      { !uploaded && !uploadFailed && !uploading ? <Popup header='Not Uploaded' content='This structure has not been Uploaded'
        trigger={<Label icon='upload' color='yellow' content='Click to sync' />}
      /> : null }
      { uploaded ? <Popup header='Uploaded!' content='This structure has been Uploaded'
        trigger={<Label color='green' icon='check' content='Synced' />}
      /> : null }
      { !uploading && !uploaded && uploadFailed ? <Popup header='Upload Error' content='Try uploading again!'
        trigger={<Label color='red' icon='bug' content='Sync error' />}
      /> : null }
      { uploading ? <Popup header='Uploading...' content='Fingers crossed!'
        trigger={<Label icon={{ name: 'circle notch', loading: true }} content='Uploading...' />}
      /> : null }
    </Label.Group>
  </Segment>
)

StructureItem.propTypes = {
  guid: PropTypes.string,
  fields: PropTypes.object,
  uploaded: PropTypes.bool,
  uploadFailed: PropTypes.bool,
  uploading: PropTypes.bool,
  color: PropTypes.string
}

const StructureItemProps = withProps(({ uploaded, uploadFailed, uploading }) => {
  let color = null
  if (!uploaded && !uploadFailed && !uploading) color = 'yellow'
  else if (!uploading && !uploaded && uploadFailed) color = 'red'
  return { color }
})(StructureItem)

export default ProjList

function getHeader (fields) {
  try {
    return fields.structureID.value
  }
  catch (e) {
    return <span style={{ color: 'red' }} >NO NAME</span>
  }
}

function getMeta (fields) {
  let observationDate
  let designerName
  try {
    observationDate = moment(new Date(fields.observationDate.value))
  }
  catch (e) { }
  try {
    designerName = fields.designerName.value
  }
  catch (e) { }

  return <div>
    <div>{designerName ? <span>Created by: <strong>{designerName}</strong> </span> : null}</div>
    <div style={{ fontSize: '0.9rem', color: '#888' }}>{observationDate ? <span> Last Edited: <strong>{observationDate.format('dddd, MMMM Do') }</strong> </span> : null}</div>
  </div>
}
