import React from 'react'
import PropTypes from 'prop-types'
import { Image, Icon, Popup, Card, Button } from 'semantic-ui-react'
import { compose, withState } from 'recompose'

const MarkerIcon = ({ project, open }) => (
  <Popup
    flowing
    hoverable={!open}
    open={open}
    className='mapPopup'
    trigger={
      <Icon
        style={{ textShadow: '2px 2px 2px #00000099' }}
        className='markerIcon'
        name='map marker'
        size='large'
        color='red'
      />
    }>
    <Card link>
      <Image src={project.Banner} />
      <Card.Content>
        <Card.Header>{project.Title}</Card.Header>
        <Card.Meta>
          <span className='date'>Joined in 2015</span>
        </Card.Meta>
        <Card.Description>{project.Watershed}</Card.Description>
      </Card.Content>
      <Card.Content extra>
        <Button>Open Project</Button>
      </Card.Content>
    </Card>
  </Popup>
)

MarkerIcon.propTypes = {
  project: PropTypes.object,
  open: PropTypes.bool
}

export default MarkerIcon

/**
 * This is a popup state and a bit hard to test in cosmos so I write a wrapper
 * with a toggle
 */
const Wrapped = ({ open, setOpen, ...props }) => (
  <div>
    <div style={{ height: 500, width: 400, border: '2px dashed #666666' }}>
      <MarkerIcon open={open} {...props} />
    </div>
    <Button color='yellow' onClick={() => setOpen(!open)} content='Keep Me Open' />
  </div>
)

Wrapped.propTypes = {
  open: PropTypes.bool,
  setOpen: PropTypes.funv
}

export const IconWrapper = compose(
  withState('open', 'setOpen', false)
)(Wrapped)
