import React from 'react'
import PropTypes from 'prop-types'
import Note from './Note'
import { List, Image, Header, Segment, Item } from 'semantic-ui-react'
import Placeholder from '../../misc/Placeholder'
import { compose, withHandlers } from 'recompose'
import { withReduxField } from '../../../redux/withRedux'

const ImageListPicker = ({ title, options, placeholder, multiple, value, hasNote, note, notePrompt, setValue, setNote }) => (
  <Segment>
    <Header as='h3'>{title}</Header>
    <List selection>
      {options.map((op, idx) => <ImageListPickerItem
        setValue={setValue}
        active={value && (multiple ? value.indexOf(op.value) > -1 : value === op.value)}
        placeholder={placeholder}
        key={idx}
        {...op} />)}
    </List>
    {!hasNote ? null
      : <Note
        label={notePrompt}
        value={note}
        setValue={setNote}
        modalTitle={`${title}: ${notePrompt}`}
      />}
  </Segment>
)

ImageListPicker.propTypes = {
  title: PropTypes.string,
  multiple: PropTypes.bool,
  placeholder: PropTypes.object, // This is for design only
  options: PropTypes.array,
  value: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.number,
    PropTypes.string
  ]),
  setValue: PropTypes.func,

  // Now the note stuff
  note: PropTypes.string,
  notePrompt: PropTypes.string,
  hasNote: PropTypes.bool,
  setNote: PropTypes.func
}

const ImageListPickerItem = ({ image, text, active, value, setValue, subText, placeholder }) => (
  <List.Item active={active} onClick={() => setValue(value)}>
    {
      placeholder
        ? <Placeholder {...placeholder} minHeight={200} />
        : <Image src={image} width={200} />
    }
    <List.Content>
      <List.Header as='a'>{text}</List.Header>
      <List.Description>
        {subText}
      </List.Description>
    </List.Content>
  </List.Item>
)

ImageListPickerItem.propTypes = {
  image: PropTypes.string,
  value: PropTypes.string,
  text: PropTypes.string,
  subText: PropTypes.string,
  placeholder: PropTypes.object,
  active: PropTypes.bool,
  setValue: PropTypes.func
}

const Present = ({ title, value, options, note }) => (
  <Item>
    <Item.Content>
      <Item.Header as='h4'>{title}</Item.Header>
      <Item.Description>
        {printValues(value, options)}
      </Item.Description>
      {note ? <Item.Extra>Note: <em>{note}</em></Item.Extra> : null }
    </Item.Content>
  </Item>
)
function printValues (value, options) {
  console.log('VALUE', value)
  if (Array.isArray(value) && value.length > 0) {
    return (
      <ul>
        {value.map((val, idx) => (<li key={`item-${idx}`}>{options.find(op => op.value === val).label}</li>)) }
      </ul>
    )
  }
  else if (value && !Array.isArray(value)) {
    return options.find(op => op.value === value).text
  }
  else {
    return <h4 style={{ color: 'red' }}>Not Entered</h4>
  }
}

Present.propTypes = ImageListPicker.propTypes

export default compose(
  withReduxField,
  // Wrap setvalue because it can be multiples
  withHandlers({
    setValue: ({ value, setValue, multiple }) => (newVal) => {
      if (!multiple) {
        setValue(newVal)
      }
      else {
        let newValues = []
        if (Array.isArray(value)) newValues = value.slice(0)
        if (newValues.indexOf(newVal) < 0) newValues.push(newVal)
        else newValues = newValues.splice(newValues.indexOf(newVal), 1)
        setValue(newValues)
      }
    }
  })
)(ImageListPicker)
