import React from 'react'
import PropTypes from 'prop-types'
import { Icon } from 'semantic-ui-react'
import BaseMap from './BaseMap'
import { AutoSizer } from 'react-virtualized'
import { Marker } from 'react-map-gl'

const MarkerMap = ({ latitude, longitude, ...props }) => (
  <div style={{ minHeight: '100%' }}>
    <AutoSizer>
      {({ height, width }) => (
        <BaseMap
          {...props}
          longitude={longitude}
          latitude={latitude}
          size={{ height: height < 500 ? 500 : height, width }}
        >
          {
            // Render props here
            (viewport) => (
              <Marker viewport={viewport}
                longitude={longitude}
                latitude={latitude}
                offsetLeft={-20}
                offsetTop={-30}
              >
                <Icon
                  style={{ textShadow: '2px 2px 2px #00000099' }}
                  name='map marker'
                  size='big'
                  color='red'
                />
              </Marker>
            )
          }
        </BaseMap>
      )}
    </AutoSizer>
  </div>
)

MarkerMap.propTypes = {
  latitude: PropTypes.number,
  longitude: PropTypes.number
}

export default MarkerMap
