import BaseMap from './BaseMap'
import MarkerMap from './MarkerMap'
import MarkersMap from './MarkersMap'
import config, { mapBoxToken } from '../../../config'
import _ from 'lodash'

const fakeGeoSeed = {
  latitude: 49.3050394,
  longitude: -123.1398254
}

const randomLatLngs = []
_.times(_.random(10, 40), () => {
  randomLatLngs.push({
    latitude: fakeGeoSeed.latitude + _.random(-0.002, 0.002),
    longitude: fakeGeoSeed.longitude + _.random(-0.002, 0.002)
  })
})

const fixtures = []
const devFixtures = [
  {
    name: 'BaseMap',
    component: BaseMap,
    props: {
      // maxBounds,
      mapboxApiAccessToken: mapBoxToken,
      size: {
        height: 600,
        width: 800
      }
    }
  },
  {
    name: 'Custom Syle',
    component: BaseMap,
    props: {
      // maxBounds,
      mapboxApiAccessToken: mapBoxToken,
      mapStyles: [
        {
          toolTip: 'WHOA',
          icon: 'image',
          color: 'blue',
          style: require('./styles/mockmap.json')
        }
      ],
      size: {
        height: 600,
        width: 800
      }
    }
  },
  {
    name: 'Marker Map',
    component: MarkerMap,
    props: {
      // maxBounds,
      mapboxApiAccessToken: mapBoxToken,
      lockPan: true,
      maxZoom: 18,
      minZoom: 2,
      latitude: 49.3050394,
      longitude: -123.1398254
    }
  },
  {
    name: 'Multiple Markers Map',
    component: MarkersMap,
    props: {
      // maxBounds,
      data: randomLatLngs,
      mapboxApiAccessToken: mapBoxToken,
      maxZoom: 18,
      minZoom: 2
    }
  }
]

const finalFixes = [...fixtures, ...(config.env === 'development' ? devFixtures : [])].map(fix => {
  console.log('fix.component.displayName', fix)
  fix.component.displayName = `Common/Maps/${fix.name}`
  return fix
})

export default finalFixes
