
import React from 'react'
import PropTypes from 'prop-types'
import BaseMap from '../common/maps/BaseMap'
import { mapBoxToken } from '../../config'
import { AutoSizer } from 'react-virtualized'
import { Marker } from 'react-map-gl'
import { findBounds, findCenter } from '../../lib/mapUtil'
import MarkerIcon from '../common/maps/MarkerIcon'
import Topbar from './Topbar'
import { Dropdown } from 'semantic-ui-react'
import { compose, withProps } from 'recompose'
import './style.css'

const ProjectsMap = ({ projects, center, pts }) => (
  <div className='PageWrap'>
    <Topbar />
    <div className='mapWrapper'>
      <AutoSizer>
        {({ height, width }) => (
          <BaseMap
            longitude={center[0]}
            latitude={center[1]}
            mapboxApiAccessToken={mapBoxToken}
            maxBounds={findBounds(pts)}
            size={{ height: height < 500 ? 500 : height, width }}
            maxZoom={18}
            minZoom={2}
          >
            {
              // Render props here
              (viewport) => projects.map((project, idx) => (
                (
                  <Marker viewport={viewport} key={`mapmarker${idx}`}
                    longitude={project.Longitude}
                    latitude={project.Latitude}
                    offsetLeft={0}
                    offsetTop={0}
                  >
                    <MarkerIcon project={project} />
                  </Marker>
                )
              ))

            }
            <Dropdown text='Filter' icon='filter' color='blue' floating labeled button className='icon'>
              <Dropdown.Menu>
                <Dropdown.Header icon='tags' content='Filter by tag' />
                <Dropdown.Item>Important</Dropdown.Item>
                <Dropdown.Item>Announcement</Dropdown.Item>
                <Dropdown.Item>Discussion</Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
          </BaseMap>
        )}
      </AutoSizer>
    </div>
  </div>
)
export default compose(
  withProps(({ projects }) => {
    const pts = projects
      .filter(p => p.Latitude && p.Longitude)
      .map(p => ([p.Longitude, p.Latitude]))
    const center = findCenter(pts)
    return { pts, center }
  })
)(ProjectsMap)

ProjectsMap.propTypes = {
  projects: PropTypes.array,
  center: PropTypes.array,
  pts: PropTypes.array
}
